/**
 * Service de chargement progressif pour visualisations 3D
 * Optimise les performances en adaptant la qualit du rendu aux capacits de l'appareil
 * et en utilisant un chargement progressif des dtails
 */
import * as THREE from 'three';
import { mergeVertices } from 'three/examples/jsm/utils/BufferGeometryUtils.js';
import featureFlagsService from './featureFlags';
import apiCacheService, { CACHE_STRATEGIES } from './apiCache';

// Niveaux de dtail (LOD) pour le chargement progressif
const DETAIL_LEVELS = {
  ULTRA_LOW: 'ultra-low',   // Pour appareils trs limits ou chargement initial
  LOW: 'low',               // Pour appareils mobiles bas de gamme
  MEDIUM: 'medium',         // Pour la plupart des mobiles et tablettes
  HIGH: 'high',             // Pour ordinateurs de bureau standard
  ULTRA: 'ultra'            // Pour ordinateurs puissants
};

// Paramtres pour chaque niveau de dtail
const DETAIL_PARAMS = {
  [DETAIL_LEVELS.ULTRA_LOW]: {
    segmentCount: 10,       // Nombre de segments pour les courbes
    textureSize: 128,       // Taille des textures en pixels
    shadowsEnabled: false,  // Dsactiver les ombres
    lightCount: 1,          // Nombre de sources lumineuses
    terrainResolution: 8,   // Rsolution du terrain (1 = pleine rsolution)
    maxVisibleElements: 20  // Nombre maximum d'lments visibles
  },
  [DETAIL_LEVELS.LOW]: {
    segmentCount: 20,
    textureSize: 256,
    shadowsEnabled: false,
    lightCount: 2,
    terrainResolution: 4,
    maxVisibleElements: 50
  },
  [DETAIL_LEVELS.MEDIUM]: {
    segmentCount: 32,
    textureSize: 512,
    shadowsEnabled: true,
    lightCount: 2,
    terrainResolution: 2,
    maxVisibleElements: 100
  },
  [DETAIL_LEVELS.HIGH]: {
    segmentCount: 64,
    textureSize: 1024,
    shadowsEnabled: true,
    lightCount: 3,
    terrainResolution: 1,
    maxVisibleElements: 200
  },
  [DETAIL_LEVELS.ULTRA]: {
    segmentCount: 128,
    textureSize: 2048,
    shadowsEnabled: true,
    lightCount: 4,
    terrainResolution: 1,
    maxVisibleElements: 500
  }
};

// Tailles des textures optimises pour mobile
const MOBILE_TEXTURE_SIZES = {
  thumbnail: 128,
  small: 256,
  medium: 512,
  large: 1024
};

// Seuils de mmoire pour les appareils mobiles (en Mo)
const MEMORY_THRESHOLDS = {
  critical: 150,  // Seuil critique - libration immdiate des ressources
  warning: 300,   // Seuil d'avertissement - rduire la qualit
  optimal: 500    // Seuil optimal - fonctionnement normal
};

// Gomtries simplifies pour remplacer les modles complexes  distance
const SIMPLE_GEOMETRIES = {
  tree: new THREE.BoxGeometry(1, 2, 1),
  rock: new THREE.SphereGeometry(1, 4, 4),
  building: new THREE.BoxGeometry(1, 1, 1)
};

/**
 * Class principale pour le chargement progressif des visualisations 3D
 */
class Progressive3DLoader {
  constructor() {
    this.progressiveModeEnabled = true;
    this.currentDetailLevel = DETAIL_LEVELS.MEDIUM;
    this.deviceCapabilities = null;
    this.loadedModels = new Map();
    this.loadingProgress = {};
    this.worker = null;
    this.frustumCullingEnabled = true;
    this.lastRenderTime = 0;
    this.targetFrameRate = 30;
    this.renderTimeHistory = [];
    this.adaptiveQualityEnabled = true;
    this.isInitialized = false;
    this.texturePool = new Map(); // Pool de textures rutilisables
    this.activeRenderers = new Set(); // Ensemble des renderers actifs
    this.memoryMonitorInterval = null;
    this.contextLossHandled = false;
    this.lowMemoryMode = false;
    this.deviceMemory = null;
    this.isIOS = false;
    this.estimatedMemoryUsage = 0;
  }

  /**
   * Initialise le service de chargement progressif
   */
  async initialize() {
    if (this.isInitialized) return;
    
    try {
      // Vrifier si le feature flag est activ
      const progressiveLoadingEnabled = featureFlagsService.isEnabled('enableProgressiveLoading3D');
      
      if (!progressiveLoadingEnabled) {
        console.info('Chargement progressif 3D dsactiv via feature flags');
        this.progressiveModeEnabled = false;
        this.isInitialized = true;
        return;
      }
      
      // Dtecter les capacits du dispositif
      await this.detectDeviceCapabilities();
      
      // Initialiser le Web Worker si support
      if (this.deviceCapabilities.webWorkerSupport) {
        this.initializeWebWorker();
      }
      
      // Initialiser les gestionnaires d'vnements pour la perte de contexte WebGL
      this.setupContextLossHandlers();
      
      // Configurer la surveillance de la mmoire pour les appareils mobiles
      if (this.deviceCapabilities.isMobile) {
        this.setupMemoryMonitoring();
      }
      
      console.info('Service de chargement progressif 3D initialis', {
        detailLevel: this.currentDetailLevel,
        adaptiveQuality: this.adaptiveQualityEnabled,
        webWorker: this.worker !== null,
        deviceMemory: this.deviceMemory,
        isMobile: this.deviceCapabilities.isMobile,
        isIOS: this.isIOS
      });
      
      this.isInitialized = true;
    } catch (error) {
      console.error('Erreur lors de l\'initialisation du chargement progressif 3D:', error);
      // Fallback au mode standard en cas d'erreur
      this.progressiveModeEnabled = false;
      this.isInitialized = true;
    }
  }

  /**
   * Dtecte les capacits du dispositif utilis
   */
  async detectDeviceCapabilities() {
    this.deviceCapabilities = {
      webWorkerSupport: typeof Worker !== 'undefined',
      webGLVersion: 1,
      maxTextureSize: 2048,
      anisotropySupport: false,
      maxAnisotropy: 1,
      isMobile: false,
      devicePixelRatio: window.devicePixelRatio || 1
    };
    
    // Dtection des plateformes mobiles
    this.deviceCapabilities.isMobile = /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent);
    this.isIOS = /iPad|iPhone|iPod/.test(navigator.userAgent) && !window.MSStream;
    
    // Dtection de la mmoire disponible (si supporte)
    if (navigator.deviceMemory) {
      this.deviceMemory = navigator.deviceMemory;
    } else {
      // Estimation base sur l'agent utilisateur
      this.deviceMemory = this.deviceCapabilities.isMobile ? 2 : 8;
    }
    
    try {
      // Crer un canvas temporaire pour tester les capacits WebGL
      const canvas = document.createElement('canvas');
      let gl = canvas.getContext('webgl2');
      
      if (gl) {
        this.deviceCapabilities.webGLVersion = 2;
      } else {
        gl = canvas.getContext('webgl') || canvas.getContext('experimental-webgl');
        this.deviceCapabilities.webGLVersion = gl ? 1 : 0;
      }
      
      if (gl) {
        // Dtecter la taille maximale de texture
        this.deviceCapabilities.maxTextureSize = gl.getParameter(gl.MAX_TEXTURE_SIZE);
        
        // Dtecter le support d'anisotropie
        const ext = gl.getExtension('EXT_texture_filter_anisotropic') || 
                    gl.getExtension('MOZ_EXT_texture_filter_anisotropic') || 
                    gl.getExtension('WEBKIT_EXT_texture_filter_anisotropic');
        
        if (ext) {
          this.deviceCapabilities.anisotropySupport = true;
          this.deviceCapabilities.maxAnisotropy = gl.getParameter(ext.MAX_TEXTURE_MAX_ANISOTROPY_EXT);
        }
        
        // Librer les ressources du contexte temporaire
        const loseContext = gl.getExtension('WEBGL_lose_context');
        if (loseContext) {
          loseContext.loseContext();
        }
      }
      
      // Dfinir le niveau de dtail appropri en fonction des capacits dtectes
      if (this.deviceCapabilities.isMobile) {
        if (this.deviceMemory <= 2) {
          this.currentDetailLevel = DETAIL_LEVELS.LOW;
        } else {
          this.currentDetailLevel = DETAIL_LEVELS.MEDIUM;
        }
        
        // Activer automatiquement le frustum culling sur mobile
        this.frustumCullingEnabled = true;
      } else {
        if (this.deviceCapabilities.webGLVersion >= 2) {
          this.currentDetailLevel = DETAIL_LEVELS.HIGH;
        } else {
          this.currentDetailLevel = DETAIL_LEVELS.MEDIUM;
        }
      }
      
    } catch (error) {
      console.error('Erreur lors de la dtection des capacits WebGL:', error);
      this.currentDetailLevel = DETAIL_LEVELS.LOW;
    }
    
    return this.deviceCapabilities;
  }

  /**
   * Unload d'un modle et libration des ressources WebGL associes
   * @param {string} modelId Identifiant du modle
   */
  unloadModel(modelId) {
    const model = this.loadedModels.get(modelId);
    
    if (model) {
      // Libration amliore des ressources Three.js
      if (model.geometry) {
        model.geometry.dispose();
      }
      
      // Librer les textures et matriaux
      if (model.material) {
        if (Array.isArray(model.material)) {
          model.material.forEach(material => this.disposeMaterial(material));
        } else {
          this.disposeMaterial(model.material);
        }
      }
      
      // Librer les animations et mixers s'ils existent
      if (model.animations && model.mixer) {
        model.mixer.stopAllAction();
        model.mixer.uncacheRoot(model);
      }
      
      // Librer les enfants rcursivement
      if (model.children) {
        model.children.forEach(child => {
          this.disposeObject3D(child);
        });
      }
      
      // Supprimer de la scne si ajout
      if (model.parent) {
        model.parent.remove(model);
      }
      
      this.loadedModels.delete(modelId);
      
      // Mettre  jour l'estimation de la mmoire
      this.updateMemoryUsage();
      
      console.info(`Modle ${modelId} dcharg et ressources libres`);
    }
  }

  /**
   * Libre les ressources d'un matriau
   * @param {THREE.Material} material Le matriau  librer
   */
  disposeMaterial(material) {
    if (!material) return;
    
    // Librer les textures
    for (const propertyName in material) {
      const property = material[propertyName];
      if (property && property.isTexture) {
        // Si la texture est dans le pool, la marquer comme disponible
        if (this.texturePool.has(property.uuid)) {
          const poolEntry = this.texturePool.get(property.uuid);
          poolEntry.inUse = false;
        } else {
          // Sinon, librer compltement
          property.dispose();
        }
      }
    }
    
    // Librer le matriau lui-mme
    material.dispose();
  }

  /**
   * Dispose rcursivement un Object3D et toutes ses ressources
   * @param {THREE.Object3D} object L'objet  librer
   */
  disposeObject3D(object) {
    if (!object) return;
    
    // Traiter les enfants de manire rcursive
    if (object.children && object.children.length > 0) {
      // Crer une copie du tableau pour viter les problmes de modification pendant l'itration
      const children = [...object.children];
      children.forEach(child => {
        this.disposeObject3D(child);
        object.remove(child);
      });
    }
    
    // Librer la gomtrie
    if (object.geometry) {
      object.geometry.dispose();
    }
    
    // Librer le matriau
    if (object.material) {
      if (Array.isArray(object.material)) {
        object.material.forEach(material => this.disposeMaterial(material));
      } else {
        this.disposeMaterial(object.material);
      }
    }
  }

  /**
   * Mise en place des gestionnaires pour la perte de contexte WebGL
   */
  setupContextLossHandlers() {
    // Enregistrer une fonction pour traiter la perte de contexte WebGL
    window.addEventListener('webglcontextlost', (event) => {
      event.preventDefault();
      this.contextLossHandled = true;
      console.warn('Contexte WebGL perdu, tentative de rcupration...');
      
      // Arrter tous les rendus 3D en cours
      this.activeRenderers.forEach(renderer => {
        renderer.forceContextLoss();
      });
      
      // Passer en mode basse mmoire
      this.lowMemoryMode = true;
      this.currentDetailLevel = DETAIL_LEVELS.ULTRA_LOW;
      
      // Notifier l'application de la perte de contexte
      document.dispatchEvent(new CustomEvent('webgl-context-lost'));
    }, false);
    
    // Gestionnaire pour la restauration du contexte
    window.addEventListener('webglcontextrestored', () => {
      console.info('Contexte WebGL restaur');
      this.contextLossHandled = false;
      
      // Rinitialiser les renderers
      this.activeRenderers.forEach(renderer => {
        renderer.resetState();
      });
      
      // Rinitialiser les textures et matriaux si ncessaire
      this.rebuildTextures();
      
      // Notifier l'application de la restauration du contexte
      document.dispatchEvent(new CustomEvent('webgl-context-restored'));
    }, false);
  }

  /**
   * Surveille l'utilisation de la mmoire sur les appareils mobiles
   */
  setupMemoryMonitoring() {
    // Nettoyer l'ancien interval si existant
    if (this.memoryMonitorInterval) {
      clearInterval(this.memoryMonitorInterval);
    }
    
    // tablir un intervalle pour vrifier la mmoire
    this.memoryMonitorInterval = setInterval(() => {
      this.checkMemoryUsage();
    }, 30000); // Vrifier toutes les 30 secondes
    
    // Ajouter des gestionnaires pour les vnements de visibilit de page
    document.addEventListener('visibilitychange', () => {
      if (document.hidden) {
        // L'utilisateur a quitt la page, librer les ressources non essentielles
        this.releaseNonEssentialResources();
      } else {
        // L'utilisateur est revenu, vrifier la mmoire
        this.checkMemoryUsage();
      }
    });
  }

  /**
   * Vrifier l'utilisation de la mmoire et prendre des mesures si ncessaire
   */
  checkMemoryUsage() {
    // Estimation de l'utilisation de la mmoire
    this.updateMemoryUsage();
    
    // Vrifier les performances du systme
    const performanceNow = performance.now();
    const memoryUsageMB = this.estimatedMemoryUsage / (1024 * 1024);
    
    console.debug(`Utilisation estime de la mmoire 3D: ${memoryUsageMB.toFixed(2)} MB`);
    
    // Mesures bases sur l'utilisation de la mmoire
    if (memoryUsageMB > MEMORY_THRESHOLDS.critical) {
      console.warn(`Utilisation critique de la mmoire: ${memoryUsageMB.toFixed(2)} MB`);
      this.handleCriticalMemory();
    } else if (memoryUsageMB > MEMORY_THRESHOLDS.warning) {
      console.warn(`Avertissement mmoire: ${memoryUsageMB.toFixed(2)} MB`);
      this.decreaseQuality();
      this.releaseNonEssentialResources();
    } else if (memoryUsageMB < MEMORY_THRESHOLDS.optimal && this.lowMemoryMode) {
      // Si nous sommes en dessous du seuil optimal et en mode basse mmoire,
      // nous pouvons revenir  un mode normal
      this.lowMemoryMode = false;
      if (this.adaptiveQualityEnabled) {
        this.increaseQuality();
      }
    }
  }

  /**
   * Gestion du cas critique de mmoire - librer immdiatement les ressources
   */
  handleCriticalMemory() {
    this.lowMemoryMode = true;
    
    // Rduire drastiquement la qualit
    this.currentDetailLevel = DETAIL_LEVELS.ULTRA_LOW;
    
    // Librer les modles non essentiels
    this.unloadNonVisibleModels();
    
    // Vider le pool de textures
    this.clearTexturePool();
    
    // Forcer la libration de la mmoire si support
    if (window.gc) window.gc();
    
    // Notifier l'application pour qu'elle prenne des mesures supplmentaires
    document.dispatchEvent(new CustomEvent('webgl-memory-critical'));
  }

  /**
   * Libration des ressources non essentielles
   */
  releaseNonEssentialResources() {
    // Librer les textures non utilises
    this.cleanupTexturePool();
    
    // Librer les modles en cache mais non visibles
    this.unloadNonVisibleModels();
  }

  /**
   * Dcharger les modles qui ne sont pas actuellement visibles
   */
  unloadNonVisibleModels() {
    const visibleModelIds = new Set();
    
    // Obtenir les IDs des modles actuellement visibles (ceci est une simulation, 
    // l'implmentation relle dpendrait de comment la visibilit est gre)
    document.querySelectorAll('[data-model-visible="true"]').forEach(el => {
      const modelId = el.dataset.modelId;
      if (modelId) visibleModelIds.add(modelId);
    });
    
    // Dcharger les modles non visibles
    this.loadedModels.forEach((model, modelId) => {
      if (!visibleModelIds.has(modelId)) {
        this.unloadModel(modelId);
      }
    });
  }

  /**
   * Mise  jour de l'estimation de l'utilisation de la mmoire
   */
  updateMemoryUsage() {
    let totalMemoryUsage = 0;
    
    // Calculer la mmoire utilise par les gomtries
    this.loadedModels.forEach(model => {
      if (model.geometry) {
        // Estimation de la taille de la gomtrie
        if (model.geometry.attributes) {
          for (const name in model.geometry.attributes) {
            const attribute = model.geometry.attributes[name];
            if (attribute.array) {
              totalMemoryUsage += attribute.array.byteLength || 0;
            }
          }
        }
      }
      
      // Estimation de la taille des textures
      if (model.material) {
        const materials = Array.isArray(model.material) ? model.material : [model.material];
        materials.forEach(material => {
          for (const prop in material) {
            const texture = material[prop];
            if (texture && texture.isTexture && texture.image) {
              const width = texture.image.width || 0;
              const height = texture.image.height || 0;
              // Estimation de 4 octets par pixel (RGBA)
              totalMemoryUsage += width * height * 4;
            }
          }
        });
      }
    });
    
    // Ajouter la mmoire utilise par le pool de textures
    this.texturePool.forEach(entry => {
      const texture = entry.texture;
      if (texture && texture.image) {
        const width = texture.image.width || 0;
        const height = texture.image.height || 0;
        totalMemoryUsage += width * height * 4;
      }
    });
    
    this.estimatedMemoryUsage = totalMemoryUsage;
    return totalMemoryUsage;
  }

  /**
   * Gestion d'un pool de textures pour la rutilisation
   * @param {string} url URL de la texture
   * @param {object} options Options de la texture
   * @returns {THREE.Texture} Texture du pool ou nouvelle texture
   */
  getTextureFromPool(url, options = {}) {
    // Rechercher une texture disponible dans le pool
    for (const [uuid, entry] of this.texturePool.entries()) {
      if (!entry.inUse && entry.url === url) {
        entry.inUse = true;
        return entry.texture;
      }
    }
    
    // Si aucune texture n'est disponible, en crer une nouvelle
    const texture = new THREE.TextureLoader().load(url);
    
    // Appliquer les options
    if (options.anisotropy && this.deviceCapabilities.anisotropySupport) {
      texture.anisotropy = Math.min(
        options.anisotropy,
        this.deviceCapabilities.maxAnisotropy
      );
    }
    
    // Ajouter au pool
    this.texturePool.set(texture.uuid, {
      texture,
      url,
      inUse: true,
      lastUsed: Date.now()
    });
    
    return texture;
  }

  /**
   * Nettoyage des textures non utilises dans le pool
   */
  cleanupTexturePool() {
    const now = Date.now();
    const unusedTimeout = 60000; // 1 minute
    
    for (const [uuid, entry] of this.texturePool.entries()) {
      if (!entry.inUse && (now - entry.lastUsed > unusedTimeout)) {
        entry.texture.dispose();
        this.texturePool.delete(uuid);
      }
    }
  }

  /**
   * Vider compltement le pool de textures
   */
  clearTexturePool() {
    for (const [uuid, entry] of this.texturePool.entries()) {
      entry.texture.dispose();
    }
    this.texturePool.clear();
  }

  /**
   * Reconstruire les textures aprs une perte de contexte
   */
  rebuildTextures() {
    // Recrer les textures qui taient en utilisation
    const texturesToRebuild = new Map();
    
    // Collecter les textures qui doivent tre reconstruites
    this.texturePool.forEach((entry, uuid) => {
      if (entry.inUse) {
        texturesToRebuild.set(uuid, {
          url: entry.url,
          options: entry.options
        });
      }
    });
    
    // Vider le pool actuel
    this.clearTexturePool();
    
    // Reconstruire les textures ncessaires
    texturesToRebuild.forEach((info, uuid) => {
      const texture = this.getTextureFromPool(info.url, info.options);
      
      // Mettre  jour les rfrences dans les matriaux si ncessaire
      this.loadedModels.forEach(model => {
        if (model.material) {
          const materials = Array.isArray(model.material) ? model.material : [model.material];
          materials.forEach(material => {
            for (const prop in material) {
              if (material[prop] && material[prop].isTexture && material[prop].uuid === uuid) {
                material[prop] = texture;
              }
            }
          });
        }
      });
    });
  }

  /**
   * Enregistre un renderer pour surveillance
   * @param {THREE.WebGLRenderer} renderer Le renderer  surveiller
   */
  registerRenderer(renderer) {
    if (renderer && !this.activeRenderers.has(renderer)) {
      this.activeRenderers.add(renderer);
      
      // Configurer le renderer pour optimiser les performances mobiles
      if (this.deviceCapabilities.isMobile) {
        renderer.setPixelRatio(Math.min(this.deviceCapabilities.devicePixelRatio, 2));
        renderer.shadowMap.enabled = this.currentDetailLevel !== DETAIL_LEVELS.ULTRA_LOW && 
                                     this.currentDetailLevel !== DETAIL_LEVELS.LOW;
        renderer.shadowMap.autoUpdate = false; // Mettre  jour manuellement pour conomiser des ressources
      }
    }
  }

  /**
   * Dsenregistre un renderer
   * @param {THREE.WebGLRenderer} renderer Le renderer  dsenregistrer
   */
  unregisterRenderer(renderer) {
    if (renderer && this.activeRenderers.has(renderer)) {
      this.activeRenderers.delete(renderer);
    }
  }

  /**
   * Optimise les gomtries pour les appareils mobiles
   * @param {THREE.BufferGeometry} geometry La gomtrie  optimiser
   * @returns {THREE.BufferGeometry} La gomtrie optimise
   */
  optimizeGeometryForMobile(geometry) {
    if (!geometry || !this.deviceCapabilities.isMobile) return geometry;
    
    // Crer une copie de la gomtrie
    const optimized = geometry.clone();
    
    // Rduire la complexit pour les appareils mobiles
    if (this.currentDetailLevel === DETAIL_LEVELS.ULTRA_LOW || this.currentDetailLevel === DETAIL_LEVELS.LOW) {
      // Utiliser Three.js BufferGeometryUtils pour simplifier la gomtrie
      if (mergeVertices) {
        return mergeVertices(optimized, 0.01);
      }
    }
    
    return optimized;
  }

  /**
   * Libre toutes les ressources
   */
  dispose() {
    // Arrter le Web Worker
    if (this.worker) {
      this.worker.terminate();
      this.worker = null;
    }
    
    // Arrter la surveillance de la mmoire
    if (this.memoryMonitorInterval) {
      clearInterval(this.memoryMonitorInterval);
      this.memoryMonitorInterval = null;
    }
    
    // Librer tous les modles et leurs ressources
    this.loadedModels.forEach((model, modelId) => {
      this.unloadModel(modelId);
    });
    
    // Librer les gomtries simplifies
    for (const key in SIMPLE_GEOMETRIES) {
      if (SIMPLE_GEOMETRIES[key]) {
        SIMPLE_GEOMETRIES[key].dispose();
      }
    }
    
    // Vider le pool de textures
    this.clearTexturePool();
    
    // Dsenregistrer tous les renderers
    this.activeRenderers.clear();
    
    // Vider le cache en mmoire
    this.loadedModels.clear();
    
    // Rinitialiser l'tat
    this.loadingProgress = {};
    this.renderTimeHistory = [];
    this.isInitialized = false;
    this.contextLossHandled = false;
    this.lowMemoryMode = false;
    
    // Forcer la collecte des dchets (suggestion pour le navigateur)
    if (window.gc) {
      window.gc();
    }
    
    console.info('Service de chargement progressif 3D libr et ressources WebGL nettoyes');
  }
}

// Crer une instance singleton du service
const progressive3DLoader = new Progressive3DLoader();

export { DETAIL_LEVELS, MOBILE_TEXTURE_SIZES };
export default progressive3DLoader;
