/**
 * Utilitaire de dtection de performance pour optimiser les visualisations 3D
 * Dtecte automatiquement les capacits de l'appareil et ajuste la qualit du rendu
 */

// Exporter les niveaux de performance pour pouvoir les utiliser dans d'autres fichiers
export const PERF_LEVELS = {
  ULTRA_LOW: 0,   // Appareils trs anciens ou trs contraints
  LOW: 1,         // Appareils d'entre de gamme ou anciens
  MEDIUM_LOW: 2,  // Appareils intermdiaires faibles
  MEDIUM: 3,      // Appareils moyens
  MEDIUM_HIGH: 4, // Appareils intermdiaires puissants
  HIGH: 5,        // Appareils haut de gamme
  ULTRA_HIGH: 6   // Appareils haut de gamme
};

class PerformanceDetector {
  constructor() {
    // Niveaux de performance
    this.PERF_LEVELS = PERF_LEVELS;
    
    // Niveaux de qualit correspondants
    this.QUALITY_PRESETS = {
      ULTRA_LOW: {
        terrainSegments: 32,
        terrainTexture: 512,
        usePostProcessing: false,
        useRealisticLighting: false,
        useShadows: false,
        drawDistance: 5000,
        useAntialiasing: false,
        useBloom: false,
        particleDensity: 0.2,
        maxPointsOfInterest: 5,
        maxBackgroundModels: 0
      },
      LOW: {
        terrainSegments: 64,
        terrainTexture: 1024,
        usePostProcessing: false,
        useRealisticLighting: false,
        useShadows: false,
        drawDistance: 8000,
        useAntialiasing: false,
        useBloom: false,
        particleDensity: 0.4,
        maxPointsOfInterest: 10,
        maxBackgroundModels: 2
      },
      MEDIUM_LOW: {
        terrainSegments: 96,
        terrainTexture: 1024,
        usePostProcessing: false,
        useRealisticLighting: true,
        useShadows: false,
        drawDistance: 10000,
        useAntialiasing: true,
        useBloom: false,
        particleDensity: 0.6,
        maxPointsOfInterest: 15,
        maxBackgroundModels: 5
      },
      MEDIUM: {
        terrainSegments: 128,
        terrainTexture: 2048,
        usePostProcessing: true,
        useRealisticLighting: true,
        useShadows: true,
        drawDistance: 12000,
        useAntialiasing: true,
        useBloom: false,
        particleDensity: 0.7,
        maxPointsOfInterest: 20,
        maxBackgroundModels: 8
      },
      MEDIUM_HIGH: {
        terrainSegments: 160,
        terrainTexture: 2048,
        usePostProcessing: true,
        useRealisticLighting: true,
        useShadows: true,
        drawDistance: 15000,
        useAntialiasing: true,
        useBloom: true,
        particleDensity: 0.8,
        maxPointsOfInterest: 30,
        maxBackgroundModels: 12
      },
      HIGH: {
        terrainSegments: 192,
        terrainTexture: 4096,
        usePostProcessing: true,
        useRealisticLighting: true,
        useShadows: true,
        drawDistance: 20000,
        useAntialiasing: true,
        useBloom: true,
        particleDensity: 0.9,
        maxPointsOfInterest: 50,
        maxBackgroundModels: 20
      },
      ULTRA_HIGH: {
        terrainSegments: 256,
        terrainTexture: 4096,
        usePostProcessing: true,
        useRealisticLighting: true,
        useShadows: true,
        drawDistance: 30000,
        useAntialiasing: true,
        useBloom: true,
        particleDensity: 1.0,
        maxPointsOfInterest: 100,
        maxBackgroundModels: 30
      }
    };
    
    // tat de dtection
    this.perfLevel = null; // Niveau de performance dtect
    this.isDetecting = false;
    this.onDetectionComplete = null;
    this.detectionTimeout = 10000; // 10 secondes max pour la dtection
    
    // Paramtres du test
    this.testDuration = 3000; // 3 secondes
    this.targetFPS = 60;
    this.minimumAcceptableFPS = 30;
    
    // Cache pour les rglages utilisateur
    this.userOverrides = {};
    this.storageKey = 'gec_performance_settings';
    
    // Niveau par dfaut en attendant la dtection
    this.defaultLevel = PERF_LEVELS.MEDIUM;
  }
  
  /**
   * Dtecte le niveau de performance de l'appareil
   * @param {Function} callback - Fonction appele une fois la dtection termine
   * @returns {Promise} Rsultat de la dtection avec niveau de performance
   */
  async detectPerformance(callback = null) {
    if (this.isDetecting) {
      console.log('Dtection de performance dj en cours');
      return;
    }
    
    // Configurer la promesse et le timeout
    this.isDetecting = true;
    this.onDetectionComplete = callback;
    
    const detectionPromise = new Promise(async (resolve) => {
      console.log('Dmarrage de la dtection de performance...');
      
      // Essayer de charger les prfrences utilisateur
      const savedSettings = this._loadUserSettings();
      if (savedSettings && savedSettings.userSelectedLevel !== undefined) {
        console.log('Utilisation des rglages utilisateur sauvegards');
        this.perfLevel = savedSettings.userSelectedLevel;
        this.isDetecting = false;
        if (this.onDetectionComplete) this.onDetectionComplete(this.perfLevel);
        resolve({
          level: this.perfLevel,
          preset: this._getQualityPreset(),
          source: 'user_preference'
        });
        return;
      }
      
      // Dtection multi-facteurs
      try {
        // 1. Niveau initial bas sur l'appareil et le navigateur
        const initialLevel = this._detectInitialLevel();
        
        // 2. Test de FPS pour validation
        const fpsResult = await this._runFPSTest();
        
        // 3. Dterminer le niveau final bas sur une combinaison de facteurs
        this.perfLevel = this._determineLevelFromFPS(fpsResult, initialLevel);
        
        // 4. Ajustements additionnels bass sur la mmoire disponible et la rsolution
        this.perfLevel = this._adjustForMemoryAndResolution(this.perfLevel);
        
        console.log(`Dtection termine: niveau de performance ${this.perfLevel}`);
        
        // Sauvegarder le rsultat pour les futures visites
        this._saveDetectionResult();
        
        // Appeler le callback si fourni
        if (this.onDetectionComplete) this.onDetectionComplete(this.perfLevel);
        
        this.isDetecting = false;
        resolve({
          level: this.perfLevel,
          preset: this._getQualityPreset(),
          source: 'detection',
          fps: fpsResult
        });
      } catch (error) {
        console.error('Erreur lors de la dtection de performance:', error);
        // En cas d'erreur, utiliser un niveau par dfaut scuris
        this.perfLevel = PERF_LEVELS.MEDIUM_LOW;
        this.isDetecting = false;
        
        if (this.onDetectionComplete) this.onDetectionComplete(this.perfLevel);
        
        resolve({
          level: this.perfLevel,
          preset: this._getQualityPreset(),
          source: 'error_fallback',
          error: error.message
        });
      }
    });
    
    // Ajouter un timeout de scurit
    const timeoutPromise = new Promise((resolve) => {
      setTimeout(() => {
        if (this.isDetecting) {
          console.warn('Timeout de dtection de performance');
          this.isDetecting = false;
          this.perfLevel = this.defaultLevel;
          
          if (this.onDetectionComplete) this.onDetectionComplete(this.perfLevel);
          
          resolve({
            level: this.perfLevel,
            preset: this._getQualityPreset(),
            source: 'timeout'
          });
        }
      }, this.detectionTimeout);
    });
    
    // Retourne le premier qui termine: la dtection ou le timeout
    return Promise.race([detectionPromise, timeoutPromise]);
  }
  
  /**
   * Obtient les rglages de qualit correspondant au niveau dtect
   * @param {number} customLevel - Niveau personnalis (optionnel)
   * @returns {Object} Rglages de qualit
   */
  getQualitySettings(customLevel = null) {
    // Si pas encore dtect, utiliser le niveau par dfaut
    if (this.perfLevel === null && customLevel === null) {
      console.warn('Niveau de performance non dtect, utilisation du niveau par dfaut');
      return this._getLevelPreset(this.defaultLevel);
    }
    
    const level = customLevel !== null ? customLevel : this.perfLevel;
    const preset = this._getLevelPreset(level);
    
    // Appliquer les overrides utilisateur
    return { ...preset, ...this.userOverrides };
  }
  
  /**
   * Dfinit manuellement le niveau de qualit
   * @param {number} level - Niveau de qualit
   * @param {boolean} save - Sauvegarder le rglage pour les visites futures
   * @returns {Object} Nouveaux rglages
   */
  setQualityLevel(level, save = true) {
    if (!Object.values(PERF_LEVELS).includes(level)) {
      console.error('Niveau de qualit invalide');
      return this.getQualitySettings();
    }
    
    this.perfLevel = level;
    
    if (save) {
      this._saveUserSettings({ userSelectedLevel: level });
    }
    
    return this.getQualitySettings();
  }
  
  /**
   * Remplace certains paramtres de qualit
   * @param {Object} overrides - Paramtres  remplacer
   * @param {boolean} save - Sauvegarder les overrides
   * @returns {Object} Rglages mis  jour
   */
  overrideSettings(overrides, save = true) {
    this.userOverrides = { ...this.userOverrides, ...overrides };
    
    if (save) {
      this._saveUserSettings({ overrides: this.userOverrides });
    }
    
    return this.getQualitySettings();
  }
  
  /**
   * Rinitialise tous les paramtres  leur valeur dtecte
   * @param {boolean} clearSaved - Effacer les prfrences sauvegardes
   * @returns {Object} Rglages rinitialiss
   */
  resetToDetectedSettings(clearSaved = true) {
    this.userOverrides = {};
    
    if (clearSaved) {
      localStorage.removeItem(this.storageKey);
    }
    
    // Re-dtecter si ncessaire
    if (this.perfLevel === null) {
      this.detectPerformance();
      return this._getLevelPreset(this.defaultLevel);
    }
    
    return this.getQualitySettings();
  }
  
  /**
   * Prcharge les ressources  basse rsolution puis amliore progressivement
   * @param {Function} preloadCallback - Fonction appele pour prcharger  basse rsolution
   * @param {Function} upgradeCallback - Fonction appele pour amliorer la qualit
   * @returns {Promise} Rsultat du prchargement
   */
  async progressiveLoad(preloadCallback, upgradeCallback) {
    // Assurer que la dtection est termine
    if (this.perfLevel === null) {
      await this.detectPerformance();
    }
    
    // Dmarrer avec les paramtres de qualit basse, quelle que soit la dtection
    const lowQualitySettings = this._getLevelPreset(PERF_LEVELS.LOW);
    
    // Prcharger avec la qualit basse
    try {
      await preloadCallback(lowQualitySettings);
      
      // Passer aux rglages dtects (amlioration progressive)
      const targetSettings = this.getQualitySettings();
      
      // Si le niveau cible est suprieur au niveau bas
      if (this.perfLevel > PERF_LEVELS.LOW) {
        // Calculer les paliers intermdiaires pour une transition fluide
        const steps = this.perfLevel - PERF_LEVELS.LOW;
        
        for (let i = 1; i <= steps; i++) {
          // Niveau intermdiaire
          const intermediateLevel = PERF_LEVELS.LOW + i;
          const intermediateSettings = this._getLevelPreset(intermediateLevel);
          
          // Pause pour permettre au rendu de se stabiliser
          await new Promise(resolve => setTimeout(resolve, 300));
          
          // Appliquer le niveau intermdiaire
          await upgradeCallback(intermediateSettings, i, steps);
        }
      }
      
      return { success: true, finalSettings: targetSettings };
    } catch (error) {
      console.error('Erreur lors du chargement progressif:', error);
      return { 
        success: false, 
        error: error.message,
        // Rester avec les paramtres basse qualit en cas d'chec
        finalSettings: lowQualitySettings 
      };
    }
  }
  
  // ---------- Mthodes prives ----------
  
  /**
   * Dtecte le niveau initial bas sur l'appareil et le navigateur
   * @returns {number} Niveau initial estim
   * @private
   */
  _detectInitialLevel() {
    // Vrifier si on est sur mobile
    const isMobile = /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent);
    
    // Vrifier si c'est Safari (peut avoir des problmes avec WebGL)
    const isSafari = /^((?!chrome|android).)*safari/i.test(navigator.userAgent);
    
    // Vrifier la mmoire disponible (si l'API est disponible)
    let memoryScore = 3; // Score moyen par dfaut
    if (navigator.deviceMemory) {
      // deviceMemory donne la RAM en GB (0.25, 0.5, 1, 2, 4, 8)
      memoryScore = Math.min(6, Math.max(1, Math.floor(navigator.deviceMemory)));
    }
    
    // Vrifier le nombre de curs CPU (si disponible)
    let cpuScore = 3; // Score moyen par dfaut
    if (navigator.hardwareConcurrency) {
      // chelle proportionnelle jusqu' 16 curs
      cpuScore = Math.min(6, Math.max(1, Math.floor(navigator.hardwareConcurrency / 2)));
    }
    
    // Vrifier la rsolution de l'cran
    const pixelCount = window.screen.width * window.screen.height;
    const resolutionScore = Math.min(6, Math.max(1, Math.floor(pixelCount / (1280 * 720))));
    
    // Calculer le score initial
    let score = Math.round((memoryScore + cpuScore + resolutionScore) / 3);
    
    // Ajustements particuliers
    if (isMobile) score = Math.min(score, 4); // Plafonner les mobiles  HIGH (pas ULTRA)
    if (isSafari) score = Math.min(score, 3); // Safari peut avoir des problmes avec les visualisations 3D avances
    
    console.log(`Dtection initiale: mobile=${isMobile}, safari=${isSafari}, mmoire=${memoryScore}, CPU=${cpuScore}, rsolution=${resolutionScore}, score=${score}`);
    
    return score;
  }
  
  /**
   * Excute un test de FPS pour mesurer les performances relles
   * @returns {Promise<Object>} Rsultats du test avec FPS moyen
   * @private
   */
  async _runFPSTest() {
    return new Promise((resolve) => {
      // Crer un lment canvas temporaire pour le test
      const canvas = document.createElement('canvas');
      canvas.width = 512;
      canvas.height = 512;
      canvas.style.position = 'absolute';
      canvas.style.left = '-9999px';  // Hors cran
      document.body.appendChild(canvas);
      
      const ctx = canvas.getContext('webgl') || canvas.getContext('experimental-webgl');
      if (!ctx) {
        document.body.removeChild(canvas);
        // Si WebGL n'est pas disponible, supposer des performances basses
        resolve({ avgFps: 15, framesCount: 0, supported: false });
        return;
      }
      
      let lastTime = performance.now();
      let frames = 0;
      let totalFps = 0;
      
      // Animation de test
      const testAnimation = () => {
        // Effacer le canvas
        ctx.clearColor(0.0, 0.0, 0.0, 1.0);
        ctx.clear(ctx.COLOR_BUFFER_BIT);
        
        // Dessiner un triangle (contenu simple pour le test)
        // (Code simplifi pour l'exemple)
        
        // Calculer le FPS
        const now = performance.now();
        const delta = now - lastTime;
        const fps = 1000 / delta;
        
        frames++;
        totalFps += fps;
        lastTime = now;
        
        // Continuer le test jusqu' la dure spcifie
        if (performance.now() - startTime < this.testDuration) {
          requestAnimationFrame(testAnimation);
        } else {
          // Test termin
          document.body.removeChild(canvas);
          
          const avgFps = totalFps / frames;
          console.log(`Test FPS termin: ${avgFps.toFixed(2)} FPS moyen sur ${frames} frames`);
          
          resolve({
            avgFps,
            framesCount: frames,
            duration: this.testDuration,
            supported: true
          });
        }
      };
      
      // Dmarrer le test
      const startTime = performance.now();
      requestAnimationFrame(testAnimation);
    });
  }
  
  /**
   * Dtermine le niveau de performance  partir des rsultats du test FPS
   * @param {Object} fpsResult - Rsultats du test FPS
   * @param {number} initialLevel - Niveau initial estim
   * @returns {number} Niveau final dtermin
   * @private
   */
  _determineLevelFromFPS(fpsResult, initialLevel) {
    if (!fpsResult.supported) {
      // WebGL non support, utiliser le niveau minimum
      return PERF_LEVELS.ULTRA_LOW;
    }
    
    const { avgFps } = fpsResult;
    
    // chelle progressive des FPS pour les diffrents niveaux
    if (avgFps < 20) return PERF_LEVELS.ULTRA_LOW;
    if (avgFps < 30) return PERF_LEVELS.LOW;
    if (avgFps < 40) return PERF_LEVELS.MEDIUM_LOW;
    if (avgFps < 50) return PERF_LEVELS.MEDIUM;
    if (avgFps < 55) return PERF_LEVELS.MEDIUM_HIGH;
    if (avgFps < 59) return PERF_LEVELS.HIGH;
    
    // Si les FPS sont excellents, considrer le niveau initial pour dcider ULTRA_HIGH
    return avgFps >= 59 && initialLevel >= PERF_LEVELS.HIGH 
      ? PERF_LEVELS.ULTRA_HIGH 
      : PERF_LEVELS.HIGH;
  }
  
  /**
   * Ajuste le niveau en fonction de la mmoire et de la rsolution
   * @param {number} level - Niveau  ajuster
   * @returns {number} Niveau ajust
   * @private
   */
  _adjustForMemoryAndResolution(level) {
    // Ajustements supplmentaires bass sur la mmoire
    if (navigator.deviceMemory) {
      if (navigator.deviceMemory <= 2 && level > PERF_LEVELS.MEDIUM) {
        level = PERF_LEVELS.MEDIUM;
      }
      if (navigator.deviceMemory <= 1 && level > PERF_LEVELS.LOW) {
        level = PERF_LEVELS.LOW;
      }
    }
    
    // Ajustements bass sur la rsolution d'cran
    const pixelCount = window.screen.width * window.screen.height;
    if (pixelCount > 2560 * 1440 && level < PERF_LEVELS.MEDIUM_HIGH) {
      // crans haute rsolution ont besoin d'au moins MEDIUM_HIGH pour un bon rendu
      level = PERF_LEVELS.MEDIUM_HIGH;
    }
    
    // Protection contre les appareils  trs haute rsolution mais faibles performances
    if (pixelCount > 3840 * 2160 && navigator.deviceMemory && navigator.deviceMemory <= 4) {
      level = Math.min(level, PERF_LEVELS.MEDIUM_HIGH);
    }
    
    return level;
  }
  
  /**
   * Obtient le preset de qualit pour un niveau donn
   * @param {number} level - Niveau de performance
   * @returns {Object} Preset de qualit
   * @private
   */
  _getLevelPreset(level) {
    switch (level) {
      case PERF_LEVELS.ULTRA_LOW:
        return this.QUALITY_PRESETS.ULTRA_LOW;
      case PERF_LEVELS.LOW:
        return this.QUALITY_PRESETS.LOW;
      case PERF_LEVELS.MEDIUM_LOW:
        return this.QUALITY_PRESETS.MEDIUM_LOW;
      case PERF_LEVELS.MEDIUM:
        return this.QUALITY_PRESETS.MEDIUM;
      case PERF_LEVELS.MEDIUM_HIGH:
        return this.QUALITY_PRESETS.MEDIUM_HIGH;
      case PERF_LEVELS.HIGH:
        return this.QUALITY_PRESETS.HIGH;
      case PERF_LEVELS.ULTRA_HIGH:
        return this.QUALITY_PRESETS.ULTRA_HIGH;
      default:
        return this.QUALITY_PRESETS.MEDIUM;
    }
  }
  
  /**
   * Obtient le preset de qualit pour le niveau dtect actuel
   * @returns {Object} Preset de qualit
   * @private
   */
  _getQualityPreset() {
    return this._getLevelPreset(this.perfLevel || this.defaultLevel);
  }
  
  /**
   * Sauvegarde le rsultat de la dtection
   * @private
   */
  _saveDetectionResult() {
    localStorage.setItem(this.storageKey, JSON.stringify({
      detectedLevel: this.perfLevel,
      timestamp: Date.now(),
      userAgent: navigator.userAgent
    }));
  }
  
  /**
   * Sauvegarde les prfrences utilisateur
   * @param {Object} settings - Paramtres  sauvegarder
   * @private
   */
  _saveUserSettings(settings) {
    const current = this._loadUserSettings() || {};
    localStorage.setItem(this.storageKey, JSON.stringify({
      ...current,
      ...settings,
      timestamp: Date.now()
    }));
  }
  
  /**
   * Charge les prfrences utilisateur
   * @returns {Object|null} Paramtres sauvegards ou null
   * @private
   */
  _loadUserSettings() {
    try {
      const saved = localStorage.getItem(this.storageKey);
      return saved ? JSON.parse(saved) : null;
    } catch (e) {
      console.error('Erreur lors du chargement des prfrences:', e);
      return null;
    }
  }
}

// Exporter une instance unique
const performanceDetector = new PerformanceDetector();
export default performanceDetector;
