/**
 * Service de gestion des Feature Flags
 * Permet l'activation/dsactivation de fonctionnalits sans redploiement
 * Supporte plusieurs sources de configuration (local, API, localStorage)
 */
import axios from 'axios';
import { useEffect, useState, createContext, useContext } from 'react';

// Valeurs par dfaut des feature flags (utilises en fallback)
const DEFAULT_FLAGS = {
  // Fonctionnalits lies  l'interface utilisateur
  enableDarkMode: false,
  showBetaFeatures: false,
  useNewNavigation: false,
  
  // Fonctionnalits lies aux dfis
  enableSevenMajorsChallenge: true,
  enableMonthlyChallenge: true,
  enableSocialSharing: true,
  
  // Fonctionnalits lies  l'API et aux performances
  enableApiCaching: true,
  enablePerformanceMonitoring: true,
  
  // Fonctionnalits exprimentales
  enable3DColVisualization: true,
  enableProgressiveLoading3D: true,  // Nouveau flag pour le chargement progressif des visualisations 3D
  enableAIRecommendations: false,
  enableRealTimeWeather: true,
  
  // Fonctionnalits lies au cache et  l'optimisation
  enableAdvancedCaching: true,      // Nouveau flag pour le systme de cache avanc
  enableOfflineMode: false,         // Nouveau flag pour le mode hors ligne
  enableLazyLoadingImages: true,    // Nouveau flag pour le chargement paresseux des images
  
  // Fonctionnalits administratives
  enableAdvancedMetrics: true,
  enableBulkOperations: false,
  
  // Fonctionnalits lies  la nutrition
  enableNutritionPlanner: true,     // Nouveau flag pour le planificateur de nutrition
  enableMealSuggestions: true,      // Nouveau flag pour les suggestions de repas
  enableColSpecificNutrition: true, // Nouveau flag pour la nutrition spcifique aux cols
  
  // Fonctionnalits lies  l'entranement
  enableTrainingPrograms: true,     // Nouveau flag pour les programmes d'entranement
  enablePerformanceAnalytics: true, // Nouveau flag pour les analyses de performance
  enableColSpecificTraining: true,  // Nouveau flag pour l'entranement spcifique aux cols
  
  // Fonctionnalits lies  la montagne
  enableMountainModule: true,       // Nouveau flag pour le module montagne
  enableRegionalTrainingPlans: true // Nouveau flag pour les plans d'entranement par rgion
};

// Types d'environnement supports
const ENVIRONMENTS = {
  DEVELOPMENT: 'development',
  STAGING: 'staging',
  PRODUCTION: 'production'
};

// Contexte React pour fournir les flags  travers l'application
const FeatureFlagsContext = createContext({
  flags: DEFAULT_FLAGS,
  isLoading: true,
  error: null,
  updateFlag: () => {},
  refreshFlags: () => {},
  isEnabled: () => false,     // Nouvelle mthode pour vrifier plus facilement un flag
  getVariant: () => null      // Nouvelle mthode pour les tests A/B
});

/**
 * Classe principale de gestion des feature flags
 */
class FeatureFlagsService {
  constructor() {
    this.flags = { ...DEFAULT_FLAGS };
    this.subscribers = [];
    this.isInitialized = false;
    this.lastFetchTime = 0;
    this.cacheDuration = 5 * 60 * 1000; // 5 minutes en ms
    this.environment = process.env.NODE_ENV || ENVIRONMENTS.DEVELOPMENT;
    this.userSegment = null;
    this.flagsHistory = {}; // Historique des changements de flags
  }

  /**
   * Initialise le service et charge les flags depuis toutes les sources
   * @returns {Promise<Object>} Les feature flags
   */
  async initialize() {
    if (this.isInitialized) {
      return this.flags;
    }

    try {
      // Dterminer le segment utilisateur pour les flags cibls
      this.determineUserSegment();
      
      // Chargement en cascade avec priorit (localStorage > API > defaults)
      await this.loadFlagsFromLocalStorage();
      await this.fetchFlagsFromApi();
      
      // Vrifier les flags qui doivent tre activs/dsactivs par environnement
      this.applyEnvironmentOverrides();
      
      // Initialiser l'historique des flags
      Object.keys(this.flags).forEach(key => {
        this.flagsHistory[key] = [{
          value: this.flags[key],
          timestamp: Date.now(),
          source: 'initialization'
        }];
      });
      
      this.isInitialized = true;
      this.notifySubscribers();
      
      return this.flags;
    } catch (error) {
      console.error('Erreur lors de l\'initialisation des feature flags:', error);
      // En cas d'erreur, utiliser les flags par dfaut
      return this.flags;
    }
  }

  /**
   * Dtermine le segment utilisateur pour une exprience personnalise
   */
  determineUserSegment() {
    try {
      // Rcuprer les informations utilisateur du localStorage
      const userInfo = JSON.parse(localStorage.getItem('userInfo') || '{}');
      
      // On peut dterminer un segment bas sur diffrents critres
      if (userInfo.role === 'admin') {
        this.userSegment = 'admin';
      } else if (userInfo.visits && userInfo.visits > 10) {
        this.userSegment = 'power_user';
      } else if (userInfo.registeredAt) {
        const registrationDate = new Date(userInfo.registeredAt);
        const oneMonthAgo = new Date();
        oneMonthAgo.setMonth(oneMonthAgo.getMonth() - 1);
        
        if (registrationDate > oneMonthAgo) {
          this.userSegment = 'new_user';
        } else {
          this.userSegment = 'regular_user';
        }
      } else {
        this.userSegment = 'anonymous';
      }
    } catch (error) {
      console.error('Erreur lors de la dtermination du segment utilisateur:', error);
      this.userSegment = 'unknown';
    }
  }

  /**
   * Applique les valeurs par dfaut spcifiques  l'environnement
   */
  applyEnvironmentOverrides() {
    // Dsactiver certaines fonctionnalits en production pour viter les problmes
    if (this.environment === ENVIRONMENTS.PRODUCTION) {
      // En production, dsactiver les fonctionnalits exprimentales par dfaut
      const productionOverrides = {
        showBetaFeatures: false,
        enableAIRecommendations: false
      };
      
      // Mais ne pas craser si explicitement activ via API ou localStorage
      Object.keys(productionOverrides).forEach(key => {
        // Vrifier si la valeur a t dfinie explicitement par l'API ou localStorage
        if (!this.flagsHistory[key] || this.flagsHistory[key].length === 0) {
          this.flags[key] = productionOverrides[key];
        }
      });
    }
    
    // Activer plus de fonctionnalits en dveloppement pour les tests
    if (this.environment === ENVIRONMENTS.DEVELOPMENT) {
      const devOverrides = {
        showBetaFeatures: true,
        enablePerformanceMonitoring: true
      };
      
      Object.assign(this.flags, devOverrides);
    }
  }

  /**
   * Charge les flags depuis le localStorage
   */
  async loadFlagsFromLocalStorage() {
    try {
      const storedFlags = localStorage.getItem('featureFlags');
      if (storedFlags) {
        const parsedFlags = JSON.parse(storedFlags);
        this.flags = { ...this.flags, ...parsedFlags };
        
        // Enregistrer la source dans l'historique
        Object.keys(parsedFlags).forEach(key => {
          if (!this.flagsHistory[key]) this.flagsHistory[key] = [];
          this.flagsHistory[key].push({
            value: parsedFlags[key],
            timestamp: Date.now(),
            source: 'localStorage'
          });
        });
      }
    } catch (error) {
      console.error('Erreur lors du chargement des feature flags du localStorage:', error);
    }
  }

  /**
   * Rcupre les flags depuis l'API
   * @param {boolean} force Force le rafrachissement mme si le cache est valide
   */
  async fetchFlagsFromApi(force = false) {
    const now = Date.now();
    
    // Vrifier si nous devons rafrachir le cache
    if (!force && (now - this.lastFetchTime) < this.cacheDuration) {
      return this.flags;
    }
    
    try {
      const response = await axios.get('/api/feature-flags', {
        params: {
          segment: this.userSegment,
          environment: this.environment
        }
      });
      
      if (response.data && response.data.flags) {
        // Enregistrer les anciennes valeurs pour le tracking des changements
        const oldFlags = { ...this.flags };
        
        // Mettre  jour les flags
        this.flags = { ...this.flags, ...response.data.flags };
        this.lastFetchTime = now;
        
        // Stocker dans localStorage pour utilisation offline
        localStorage.setItem('featureFlags', JSON.stringify(this.flags));
        
        // Mettre  jour l'historique des changements
        Object.keys(response.data.flags).forEach(key => {
          if (!this.flagsHistory[key]) this.flagsHistory[key] = [];
          this.flagsHistory[key].push({
            value: response.data.flags[key],
            previousValue: oldFlags[key],
            timestamp: now,
            source: 'api'
          });
        });
        
        this.notifySubscribers();
        this.logFlagChanges(oldFlags, this.flags);
      }
    } catch (error) {
      console.error('Erreur lors de la rcupration des feature flags depuis l\'API:', error);
      // En cas d'erreur, continuer avec les flags actuels
    }
    
    return this.flags;
  }

  /**
   * Journalise les changements de flags pour dbogage et analyses
   * @param {Object} oldFlags Anciens flags
   * @param {Object} newFlags Nouveaux flags
   */
  logFlagChanges(oldFlags, newFlags) {
    const changes = [];
    
    Object.keys(newFlags).forEach(key => {
      if (oldFlags[key] !== newFlags[key]) {
        changes.push({
          flag: key,
          oldValue: oldFlags[key],
          newValue: newFlags[key],
          timestamp: Date.now()
        });
      }
    });
    
    if (changes.length > 0) {
      console.info('Feature Flags mis  jour:', changes);
      
      // En dveloppement, afficher une notification visuelle
      if (this.environment === ENVIRONMENTS.DEVELOPMENT && typeof window !== 'undefined') {
        // Crer une notification simple pour les dveloppeurs
        this.showDevNotification(`${changes.length} feature flags mis  jour`);
      }
    }
  }

  /**
   * Affiche une notification pour les dveloppeurs
   * @param {string} message Message  afficher
   */
  showDevNotification(message) {
    if (typeof document === 'undefined') return;
    
    const notificationId = 'feature-flag-notification';
    let notification = document.getElementById(notificationId);
    
    // Crer l'lment s'il n'existe pas
    if (!notification) {
      notification = document.createElement('div');
      notification.id = notificationId;
      notification.style.position = 'fixed';
      notification.style.bottom = '20px';
      notification.style.right = '20px';
      notification.style.padding = '10px 15px';
      notification.style.backgroundColor = '#333';
      notification.style.color = 'white';
      notification.style.borderRadius = '4px';
      notification.style.zIndex = '9999';
      notification.style.opacity = '0';
      notification.style.transition = 'opacity 0.3s ease-in-out';
      document.body.appendChild(notification);
    }
    
    // Mettre  jour le contenu
    notification.textContent = message;
    notification.style.opacity = '1';
    
    // Faire disparatre aprs 3 secondes
    setTimeout(() => {
      notification.style.opacity = '0';
      setTimeout(() => {
        if (notification.parentNode) {
          notification.parentNode.removeChild(notification);
        }
      }, 300);
    }, 3000);
  }

  /**
   * Met  jour un ou plusieurs feature flags
   * @param {Object} flagUpdates Objet contenant les flags  mettre  jour
   * @param {boolean} persistToServer Si vrai, envoie les modifications au serveur
   * @returns {Promise<Object>} Les feature flags mis  jour
   */
  async updateFlags(flagUpdates, persistToServer = true) {
    // Enregistrer les anciennes valeurs
    const oldFlags = { ...this.flags };
    
    // Mettre  jour les flags localement
    this.flags = { ...this.flags, ...flagUpdates };
    
    // Stocker dans localStorage
    localStorage.setItem('featureFlags', JSON.stringify(this.flags));
    
    // Mettre  jour l'historique
    Object.keys(flagUpdates).forEach(key => {
      if (!this.flagsHistory[key]) this.flagsHistory[key] = [];
      this.flagsHistory[key].push({
        value: flagUpdates[key],
        previousValue: oldFlags[key],
        timestamp: Date.now(),
        source: 'manual_update'
      });
    });
    
    // Journaliser les changements
    this.logFlagChanges(oldFlags, this.flags);
    
    // Notifier les abonns
    this.notifySubscribers();
    
    // Persister sur le serveur si demand
    if (persistToServer) {
      try {
        await axios.post('/api/feature-flags', { 
          flags: flagUpdates,
          segment: this.userSegment,
          environment: this.environment
        });
      } catch (error) {
        console.error('Erreur lors de la mise  jour des feature flags sur le serveur:', error);
      }
    }
    
    return this.flags;
  }

  /**
   * Vrifie si un feature flag est activ
   * @param {string} flagName Nom du flag  vrifier
   * @returns {boolean} Statut du flag (true = activ)
   */
  isEnabled(flagName) {
    // Si le flag n'existe pas, retourner false
    if (!(flagName in this.flags)) {
      console.warn(`Feature flag "${flagName}" non trouv`);
      return false;
    }
    
    return this.flags[flagName] === true;
  }

  /**
   * Rcupre la valeur d'un flag pour les tests A/B ou les flags multi-valeurs
   * @param {string} flagName Nom du flag
   * @param {any} defaultValue Valeur par dfaut si le flag n'existe pas
   * @returns {any} Valeur du flag ou valeur par dfaut
   */
  getVariant(flagName, defaultValue = null) {
    if (!(flagName in this.flags)) {
      console.warn(`Feature flag variant "${flagName}" non trouv`);
      return defaultValue;
    }
    
    return this.flags[flagName];
  }

  /**
   * S'abonne aux changements de feature flags
   * @param {Function} callback Fonction appele lors d'un changement
   * @returns {Function} Fonction pour se dsabonner
   */
  subscribe(callback) {
    this.subscribers.push(callback);
    
    // Retourner une fonction de nettoyage pour le dsabonnement
    return () => {
      this.subscribers = this.subscribers.filter(sub => sub !== callback);
    };
  }

  /**
   * Notifie tous les abonns d'un changement de flags
   */
  notifySubscribers() {
    this.subscribers.forEach(callback => {
      try {
        callback(this.flags);
      } catch (error) {
        console.error('Erreur lors de la notification d\'un abonn aux feature flags:', error);
      }
    });
  }

  /**
   * Rcupre l'historique des changements pour un flag
   * @param {string} flagName Nom du flag
   * @returns {Array} Historique des changements
   */
  getFlagHistory(flagName) {
    return this.flagsHistory[flagName] || [];
  }

  /**
   * Dfinit la dure du cache
   * @param {number} durationMs Dure en milliseconds
   */
  setCacheDuration(durationMs) {
    this.cacheDuration = durationMs;
  }
}

// Crer une instance singleton du service
const featureFlagsService = new FeatureFlagsService();

/**
 * Hook React pour utiliser les feature flags dans les composants
 * @returns {Object} Objet contenant les flags et fonctions utilitaires
 */
export const useFeatureFlags = () => {
  const context = useContext(FeatureFlagsContext);
  
  if (!context) {
    throw new Error('useFeatureFlags doit tre utilis  l\'intrieur d\'un FeatureFlagsProvider');
  }
  
  return context;
};

/**
 * Fournisseur de contexte pour les feature flags
 * @param {Object} props Props du composant
 * @param {React.ReactNode} props.children Enfants du composant
 */
export const FeatureFlagsProvider = ({ children }) => {
  const [flags, setFlags] = useState(DEFAULT_FLAGS);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState(null);

  // Charger les flags au montage du composant
  useEffect(() => {
    const initializeFlags = async () => {
      try {
        setIsLoading(true);
        await featureFlagsService.initialize();
        setFlags({ ...featureFlagsService.flags });
        setError(null);
      } catch (err) {
        setError(err.message || 'Erreur lors du chargement des feature flags');
      } finally {
        setIsLoading(false);
      }
    };

    initializeFlags();

    // S'abonner aux changements
    const unsubscribe = featureFlagsService.subscribe(updatedFlags => {
      setFlags({ ...updatedFlags });
    });

    // Se dsabonner lors du dmontage
    return unsubscribe;
  }, []);

  // Fonction pour mettre  jour un flag
  const updateFlag = async (flagName, value, persistToServer = true) => {
    try {
      await featureFlagsService.updateFlags({ [flagName]: value }, persistToServer);
    } catch (err) {
      setError(err.message || 'Erreur lors de la mise  jour du feature flag');
    }
  };

  // Fonction pour rafrachir tous les flags
  const refreshFlags = async () => {
    try {
      setIsLoading(true);
      await featureFlagsService.fetchFlagsFromApi(true);
      setFlags({ ...featureFlagsService.flags });
      setError(null);
    } catch (err) {
      setError(err.message || 'Erreur lors du rafrachissement des feature flags');
    } finally {
      setIsLoading(false);
    }
  };

  // Fonction pour vrifier si un flag est activ (raccourci)
  const isEnabled = (flagName) => {
    return featureFlagsService.isEnabled(flagName);
  };

  // Fonction pour rcuprer la valeur d'un flag variant
  const getVariant = (flagName, defaultValue = null) => {
    return featureFlagsService.getVariant(flagName, defaultValue);
  };

  const value = {
    flags,
    isLoading,
    error,
    updateFlag,
    refreshFlags,
    isEnabled,
    getVariant
  };

  return (
    <FeatureFlagsContext.Provider value={value}>
      {children}
    </FeatureFlagsContext.Provider>
  );
};

// Exporter le service pour une utilisation directe si ncessaire
export default featureFlagsService;
