/**
 * Service de cache API avanc
 * Permet de rduire les appels API redondants et d'amliorer les performances
 * Supporte plusieurs stratgies de cache, invalidation intelligente et persistance
 */
import axios from 'axios';
import featureFlagsService from './featureFlags';

// Constantes pour les stratgies de cache
const CACHE_STRATEGIES = {
  NETWORK_FIRST: 'network-first',  // Essaie le rseau, utilise le cache en fallback
  CACHE_FIRST: 'cache-first',      // Utilise le cache, fait appel au rseau si expir ou absent
  STALE_WHILE_REVALIDATE: 'stale-while-revalidate', // Retourne le cache immdiatement et met  jour en arrire-plan
  NETWORK_ONLY: 'network-only',    // Toujours utiliser le rseau (dsactive le cache)
  CACHE_ONLY: 'cache-only'         // Toujours utiliser le cache (mode hors ligne)
};

// Structure de l'lment de cache
class CacheItem {
  constructor(data, params = {}) {
    this.data = data;
    this.timestamp = Date.now();
    this.expiry = this.timestamp + (params.ttl || 5 * 60 * 1000); // 5 minutes par dfaut
    this.staleExpiry = this.expiry + (params.staleTime || 60 * 60 * 1000); // 1 heure de plus pour les donnes obsoltes
    this.tags = params.tags || [];
    this.etag = params.etag || null;
    this.lastModified = params.lastModified || null;
  }

  isExpired() {
    return Date.now() > this.expiry;
  }

  isStale() {
    return this.isExpired() && Date.now() <= this.staleExpiry;
  }

  // Mthode pour tendre la dure de vie du cache
  extend(additionalTime) {
    this.expiry += additionalTime;
    this.staleExpiry += additionalTime;
  }
}

/**
 * Service principal de gestion du cache API
 */
class ApiCacheService {
  constructor() {
    this.cache = new Map();
    this.pendingRequests = new Map();
    this.isInitialized = false;
    this.storageKey = 'api_cache_data';
    this.defaultTTL = 5 * 60 * 1000; // 5 minutes par dfaut
    this.maxCacheSize = 100; // Nombre maximum d'entres dans le cache
    this.defaultStrategy = CACHE_STRATEGIES.CACHE_FIRST;
    this.networkMonitor = {
      isOnline: true,
      lastCheck: Date.now()
    };
    this.statistics = {
      hits: 0,
      misses: 0,
      errors: 0,
      networkCalls: 0
    };
    this.ttlConfigKey = 'api_cache_ttl_config';
    this.categoryTTLs = {};

    // Cration d'une instance axios personnalise
    this.axiosInstance = axios.create({
      timeout: 10000, // 10 secondes de timeout par dfaut
    });

    // Intercepteur pour ajouter les headers de cache
    this.axiosInstance.interceptors.request.use(config => {
      const cacheKey = this.generateCacheKey(config.url, config.params);
      
      // Ajouter les headers conditionnels si disponibles
      const cachedItem = this.cache.get(cacheKey);
      if (cachedItem) {
        if (cachedItem.etag) {
          config.headers['If-None-Match'] = cachedItem.etag;
        }
        if (cachedItem.lastModified) {
          config.headers['If-Modified-Since'] = cachedItem.lastModified;
        }
      }
      
      return config;
    });

    // Intercepteur pour grer les rponses 304 Not Modified
    this.axiosInstance.interceptors.response.use(
      response => {
        // Capture des headers ETag et Last-Modified
        const newHeaders = {};
        if (response.headers.etag) {
          newHeaders.etag = response.headers.etag;
        }
        if (response.headers['last-modified']) {
          newHeaders.lastModified = response.headers['last-modified'];
        }
        
        // Ajouter les headers  la rponse pour utilisation dans le cache
        response.cacheHeaders = newHeaders;
        return response;
      },
      error => {
        // Si 304 Not Modified, considrer comme un succs
        if (error.response && error.response.status === 304) {
          const cacheKey = this.generateCacheKey(
            error.config.url, 
            error.config.params
          );
          const cachedItem = this.cache.get(cacheKey);
          
          if (cachedItem) {
            // Crer une rponse simule avec les donnes du cache
            const response = {
              data: cachedItem.data,
              status: 200,
              statusText: 'OK (from cache)',
              headers: error.response.headers,
              config: error.config,
              fromCache: true
            };
            
            // Prolonger la dure de vie du cache
            cachedItem.extend(this.defaultTTL);
            this.cache.set(cacheKey, cachedItem);
            
            return response;
          }
        }
        
        return Promise.reject(error);
      }
    );
  }

  /**
   * Initialise le service de cache
   * @returns {Promise<void>}
   */
  async initialize() {
    if (this.isInitialized) return;
    
    try {
      // Charger les TTL spcifiques par catgorie depuis les feature flags
      await this.loadTTLConfigurations();
      
      // Si le caching avanc est activ, charger depuis le stockage
      if (featureFlagsService.isEnabled('enableAdvancedCaching')) {
        await this.loadCacheFromStorage();
      }
      
      // Configurer la surveillance rseau
      this.setupNetworkMonitoring();
      
      // Configurer le nettoyage priodique du cache
      this.setupPeriodicCleanup();
      
      this.isInitialized = true;
      console.info('Service de cache API initialis', {
        cacheSize: this.cache.size,
        defaultTTL: this.formatDuration(this.defaultTTL)
      });
    } catch (error) {
      console.error('Erreur lors de l\'initialisation du cache API:', error);
    }
  }

  /**
   * Configure la surveillance de la connexion rseau
   */
  setupNetworkMonitoring() {
    if (typeof window !== 'undefined') {
      // Dtecter les changements de connectivit
      window.addEventListener('online', () => {
        this.networkMonitor.isOnline = true;
        this.networkMonitor.lastCheck = Date.now();
        console.info('Connectivit rtablie - API Cache passe en mode en ligne');
      });
      
      window.addEventListener('offline', () => {
        this.networkMonitor.isOnline = false;
        this.networkMonitor.lastCheck = Date.now();
        console.info('Connectivit perdue - API Cache passe en mode hors ligne');
      });
      
      // tat initial
      this.networkMonitor.isOnline = navigator.onLine;
    }
  }

  /**
   * Configure le nettoyage priodique du cache
   */
  setupPeriodicCleanup() {
    // Nettoyer toutes les heures
    const CLEANUP_INTERVAL = 60 * 60 * 1000;
    
    if (typeof window !== 'undefined') {
      setInterval(() => this.cleanupCache(), CLEANUP_INTERVAL);
    }
  }

  /**
   * Nettoie le cache en supprimant les entres expires et en respectant la taille maximale
   */
  cleanupCache() {
    if (this.cache.size === 0) return;
    
    console.info('Nettoyage du cache API en cours...');
    const now = Date.now();
    let deleteCount = 0;
    
    // Supprimer les entres compltement expires (mme stale)
    for (const [key, item] of this.cache.entries()) {
      if (now > item.staleExpiry) {
        this.cache.delete(key);
        deleteCount++;
      }
    }
    
    // Si le cache est toujours trop grand, supprimer les entres les plus anciennes
    if (this.cache.size > this.maxCacheSize) {
      const excess = this.cache.size - this.maxCacheSize;
      const entries = Array.from(this.cache.entries())
        .sort((a, b) => a[1].timestamp - b[1].timestamp);
      
      for (let i = 0; i < excess; i++) {
        this.cache.delete(entries[i][0]);
        deleteCount++;
      }
    }
    
    console.info(`Nettoyage du cache termin: ${deleteCount} entres supprimes`);
    this.saveCacheToStorage();
  }

  /**
   * Charge le cache depuis le localStorage
   */
  async loadCacheFromStorage() {
    try {
      const storedCache = localStorage.getItem(this.storageKey);
      if (!storedCache) return;
      
      const parsedCache = JSON.parse(storedCache);
      const now = Date.now();
      let validEntries = 0;
      
      // Ne charger que les entres non compltement expires
      Object.entries(parsedCache).forEach(([key, value]) => {
        // Recrer l'objet CacheItem correctement avec ses mthodes
        const cacheItem = new CacheItem(value.data, {
          ttl: value.expiry - value.timestamp,
          staleTime: value.staleExpiry - value.expiry,
          tags: value.tags,
          etag: value.etag,
          lastModified: value.lastModified
        });
        
        // Restaurer les timestamps originaux
        cacheItem.timestamp = value.timestamp;
        cacheItem.expiry = value.expiry;
        cacheItem.staleExpiry = value.staleExpiry;
        
        // Ne pas charger les entres compltement expires
        if (now <= cacheItem.staleExpiry) {
          this.cache.set(key, cacheItem);
          validEntries++;
        }
      });
      
      console.info(`Cache charg depuis le localStorage: ${validEntries} entres valides`);
    } catch (error) {
      console.error('Erreur lors du chargement du cache depuis le localStorage:', error);
      // Si erreur, dmarrer avec un cache vide
      this.cache.clear();
    }
  }

  /**
   * Sauvegarde le cache dans le localStorage
   */
  saveCacheToStorage() {
    try {
      // Convertir le Map en objet pour le stockage
      const cacheObject = {};
      
      for (const [key, value] of this.cache.entries()) {
        cacheObject[key] = {
          data: value.data,
          timestamp: value.timestamp,
          expiry: value.expiry,
          staleExpiry: value.staleExpiry,
          tags: value.tags,
          etag: value.etag,
          lastModified: value.lastModified
        };
      }
      
      localStorage.setItem(this.storageKey, JSON.stringify(cacheObject));
    } catch (error) {
      console.error('Erreur lors de la sauvegarde du cache dans le localStorage:', error);
    }
  }

  /**
   * Gnre une cl de cache unique pour une URL et des paramtres
   * @param {string} url URL de la requte
   * @param {Object} params Paramtres de la requte
   * @returns {string} Cl de cache unique
   */
  generateCacheKey(url, params = {}) {
    // Normaliser l'URL
    const normalizedUrl = url.toLowerCase().trim();
    
    // Trier les paramtres pour assurer la cohrence des cls
    const sortedParams = {};
    if (params) {
      Object.keys(params).sort().forEach(key => {
        sortedParams[key] = params[key];
      });
    }
    
    // Crer une cl unique
    return `${normalizedUrl}|${JSON.stringify(sortedParams)}`;
  }

  /**
   * Effectue une requte GET avec gestion de cache
   * @param {string} url URL de la requte
   * @param {Object} options Options de la requte
   * @param {Object} options.params Paramtres de la requte
   * @param {string} options.strategy Stratgie de cache  utiliser
   * @param {number} options.ttl Dure de vie du cache en millisecondes
   * @param {Array<string>} options.tags Tags associs  cette entre de cache
   * @returns {Promise<any>} Donnes de la rponse
   */
  async get(url, options = {}) {
    // Initialiser le service si ce n'est pas dj fait
    if (!this.isInitialized) {
      await this.initialize();
    }
    
    const {
      params = {},
      strategy = this.defaultStrategy,
      tags = []
    } = options;
    
    // Utiliser le TTL appropri pour cette URL
    const ttl = this.getTTLForUrl(url, options);
    
    // Crer la cl de cache
    const cacheKey = this.generateCacheKey(url, params);
    
    // Vrifier si la rponse est en cache
    const cachedItem = this.cache.get(cacheKey);
    
    // Options de cache  utiliser pour la mise en cache
    const cacheOptions = { ttl, tags };
    
    // Vrifier si le caching est activ
    const isCachingEnabled = featureFlagsService.isEnabled('enableApiCaching');
    
    // Si le caching est dsactiv, faire un appel rseau direct
    if (!isCachingEnabled || strategy === CACHE_STRATEGIES.NETWORK_ONLY) {
      return this.fetchFromNetwork(url, params);
    }
    
    // Gestion du mode hors ligne forc
    if (strategy === CACHE_STRATEGIES.CACHE_ONLY) {
      if (cachedItem) {
        this.statistics.hits++;
        return { ...cachedItem.data, fromCache: true };
      } else {
        this.statistics.misses++;
        throw new Error('Aucune donne en cache et mode CACHE_ONLY activ');
      }
    }
    
    // Gestion du mode hors ligne en cas de perte de connectivit
    if (!this.networkMonitor.isOnline) {
      if (cachedItem) {
        console.info('Mode hors ligne - Utilisation du cache pour:', url);
        return { ...cachedItem.data, fromCache: true };
      } else {
        throw new Error('Aucune donne en cache et appareil hors ligne');
      }
    }
    
    // Stratgie CACHE_FIRST
    if (strategy === CACHE_STRATEGIES.CACHE_FIRST) {
      if (cachedItem && !cachedItem.isExpired()) {
        this.statistics.hits++;
        return { ...cachedItem.data, fromCache: true };
      }
      
      // Cache expir ou absent
      try {
        const response = await this.fetchFromNetwork(url, params);
        this.cacheResponse(cacheKey, response, cacheOptions);
        return response;
      } catch (error) {
        // En cas d'erreur rseau, utiliser le cache mme expir si disponible
        if (cachedItem) {
          console.warn('Erreur rseau - Utilisation du cache expir pour:', url);
          return { ...cachedItem.data, fromCache: true, stale: true };
        }
        throw error;
      }
    }
    
    // Stratgie STALE_WHILE_REVALIDATE
    if (strategy === CACHE_STRATEGIES.STALE_WHILE_REVALIDATE) {
      // Si un lment est en cache, le retourner immdiatement
      if (cachedItem) {
        // Mettre  jour en arrire-plan si expir
        if (cachedItem.isExpired()) {
          this.fetchFromNetworkAndUpdateCache(url, params, cacheKey, cacheOptions)
            .catch(error => console.error('Erreur lors de la mise  jour du cache en arrire-plan:', error));
        }
        
        this.statistics.hits++;
        return { ...cachedItem.data, fromCache: true, stale: cachedItem.isExpired() };
      }
      
      // Rien en cache, faire un appel rseau
      const response = await this.fetchFromNetwork(url, params);
      this.cacheResponse(cacheKey, response, cacheOptions);
      return response;
    }
    
    // Par dfaut: NETWORK_FIRST
    try {
      const response = await this.fetchFromNetwork(url, params);
      this.cacheResponse(cacheKey, response, cacheOptions);
      return response;
    } catch (error) {
      // En cas d'erreur rseau, utiliser le cache si disponible
      if (cachedItem) {
        console.warn('Erreur rseau - Utilisation du cache pour:', url);
        this.statistics.hits++;
        return { ...cachedItem.data, fromCache: true, stale: cachedItem.isExpired() };
      }
      throw error;
    }
  }

  /**
   * Effectue une requte POST (non mise en cache)
   * @param {string} url URL de la requte
   * @param {Object} data Donnes  envoyer
   * @param {Object} config Configuration axios supplmentaire
   * @returns {Promise<any>} Rponse de la requte
   */
  async post(url, data, config = {}) {
    return this.axiosInstance.post(url, data, config).then(response => response.data);
  }

  /**
   * Effectue une requte PUT (non mise en cache)
   * @param {string} url URL de la requte
   * @param {Object} data Donnes  envoyer
   * @param {Object} config Configuration axios supplmentaire
   * @returns {Promise<any>} Rponse de la requte
   */
  async put(url, data, config = {}) {
    return this.axiosInstance.put(url, data, config).then(response => response.data);
  }

  /**
   * Effectue une requte DELETE (non mise en cache)
   * @param {string} url URL de la requte
   * @param {Object} config Configuration axios supplmentaire
   * @returns {Promise<any>} Rponse de la requte
   */
  async delete(url, config = {}) {
    return this.axiosInstance.delete(url, config).then(response => response.data);
  }

  /**
   * Effectue un appel rseau et met  jour le cache en arrire-plan
   * @param {string} url URL de la requte
   * @param {Object} params Paramtres de la requte
   * @param {string} cacheKey Cl de cache
   * @param {Object} cacheOptions Options de cache
   * @returns {Promise<void>}
   */
  async fetchFromNetworkAndUpdateCache(url, params, cacheKey, cacheOptions) {
    try {
      const response = await this.fetchFromNetwork(url, params);
      this.cacheResponse(cacheKey, response, cacheOptions);
    } catch (error) {
      console.error('Erreur lors de la mise  jour du cache en arrire-plan:', error);
    }
  }

  /**
   * Effectue un appel rseau en grant les requtes en parallle
   * @param {string} url URL de la requte
   * @param {Object} params Paramtres de la requte
   * @returns {Promise<any>} Donnes de la rponse
   */
  async fetchFromNetwork(url, params = {}) {
    const cacheKey = this.generateCacheKey(url, params);
    
    // Vrifier si une requte identique est dj en cours
    if (this.pendingRequests.has(cacheKey)) {
      return this.pendingRequests.get(cacheKey);
    }
    
    // Crer une nouvelle promesse pour cette requte
    const requestPromise = this.axiosInstance.get(url, { params })
      .then(response => {
        // Capturer les headers ETag et Last-Modified
        const headers = response.cacheHeaders || {};
        
        this.statistics.networkCalls++;
        this.pendingRequests.delete(cacheKey);
        return {
          data: response.data,
          headers,
          fromCache: false
        };
      })
      .catch(error => {
        this.statistics.errors++;
        this.pendingRequests.delete(cacheKey);
        throw error;
      });
    
    // Enregistrer cette requte en cours
    this.pendingRequests.set(cacheKey, requestPromise);
    
    return requestPromise;
  }

  /**
   * Enregistre une rponse dans le cache
   * @param {string} cacheKey Cl de cache
   * @param {Object} response Rponse  mettre en cache
   * @param {Object} options Options de cache
   */
  cacheResponse(cacheKey, response, options = {}) {
    const { ttl = this.defaultTTL, tags = [] } = options;
    
    // Ne pas mettre en cache les rponses dj issues du cache
    if (response.fromCache) return;
    
    // Crer un objet de cache
    const cacheItem = new CacheItem(response.data, {
      ttl,
      tags,
      etag: response.headers?.etag,
      lastModified: response.headers?.lastModified
    });
    
    // Ajouter au cache
    this.cache.set(cacheKey, cacheItem);
    
    // Si le cache dpasse sa taille maximale, supprimer les entres les plus anciennes
    if (this.cache.size > this.maxCacheSize) {
      this.pruneCache();
    }
    
    // Persister dans le localStorage si ncessaire
    if (featureFlagsService.isEnabled('enableAdvancedCaching')) {
      this.saveCacheToStorage();
    }
  }

  /**
   * Rduit la taille du cache en supprimant les entres les plus anciennes
   */
  pruneCache() {
    if (this.cache.size <= this.maxCacheSize) return;
    
    const excess = this.cache.size - this.maxCacheSize;
    const entries = Array.from(this.cache.entries())
      .sort((a, b) => a[1].timestamp - b[1].timestamp);
    
    for (let i = 0; i < excess; i++) {
      this.cache.delete(entries[i][0]);
    }
  }

  /**
   * Invalide les entres de cache pour une URL spcifique
   * @param {string} url URL dont les entres doivent tre invalides
   * @param {Object} params Paramtres spcifiques (si null, toutes les entres pour cette URL sont invalides)
   */
  invalidateCache(url, params = null) {
    if (params === null) {
      // Invalider toutes les entres commenant par cette URL
      const normalizedUrl = url.toLowerCase().trim();
      
      for (const key of this.cache.keys()) {
        if (key.startsWith(normalizedUrl)) {
          this.cache.delete(key);
        }
      }
    } else {
      // Invalider uniquement l'entre spcifique
      const cacheKey = this.generateCacheKey(url, params);
      this.cache.delete(cacheKey);
    }
    
    // Persister les changements
    if (featureFlagsService.isEnabled('enableAdvancedCaching')) {
      this.saveCacheToStorage();
    }
  }

  /**
   * Invalide les entres de cache par tags
   * @param {Array<string>} tags Liste des tags  invalider
   */
  invalidateByTags(tags) {
    if (!Array.isArray(tags) || tags.length === 0) return;
    
    const keysToDelete = [];
    
    // Trouver toutes les entres correspondant aux tags
    for (const [key, item] of this.cache.entries()) {
      const hasMatchingTag = tags.some(tag => item.tags.includes(tag));
      if (hasMatchingTag) {
        keysToDelete.push(key);
      }
    }
    
    // Supprimer les entres trouves
    keysToDelete.forEach(key => this.cache.delete(key));
    
    // Persister les changements
    if (keysToDelete.length > 0 && featureFlagsService.isEnabled('enableAdvancedCaching')) {
      this.saveCacheToStorage();
    }
  }

  /**
   * Efface tout le cache
   */
  clearCache() {
    this.cache.clear();
    localStorage.removeItem(this.storageKey);
    console.info('Cache API entirement effac');
  }

  /**
   * Rcupre les statistiques d'utilisation du cache
   * @returns {Object} Statistiques d'utilisation
   */
  getStatistics() {
    return {
      ...this.statistics,
      cacheSize: this.cache.size,
      pendingRequests: this.pendingRequests.size
    };
  }

  /**
   * Configure le service de cache
   * @param {Object} config Configuration
   */
  configure(config = {}) {
    if (config.defaultTTL) this.defaultTTL = config.defaultTTL;
    if (config.maxCacheSize) this.maxCacheSize = config.maxCacheSize;
    if (config.defaultStrategy) this.defaultStrategy = config.defaultStrategy;
    
    console.info('Configuration du cache API mise  jour:', config);
  }

  /**
   * Charge les configurations TTL depuis les feature flags
   */
  async loadTTLConfigurations() {
    try {
      // Vrifier si la fonctionnalit de configuration TTL est active
      const configurationEnabled = featureFlagsService.isEnabled('enableConfigurableCacheTTL');
      
      if (configurationEnabled) {
        // Rcuprer le TTL global par dfaut si configur
        const globalTTL = featureFlagsService.getValue('defaultCacheTTL');
        if (globalTTL && typeof globalTTL === 'number' && globalTTL > 0) {
          this.defaultTTL = globalTTL * 1000; // Conversion de secondes en millisecondes
          console.info(`TTL de cache global configur  ${this.formatDuration(this.defaultTTL)}`);
        }
        
        // Rcuprer les TTL spcifiques par catgorie
        const categoryTTLs = featureFlagsService.getValue('categorySpecificTTLs') || {};
        
        for (const [category, ttl] of Object.entries(categoryTTLs)) {
          if (typeof ttl === 'number' && ttl > 0) {
            this.categoryTTLs[category] = ttl * 1000; // Conversion de secondes en millisecondes
            console.info(`TTL de cache pour la catgorie "${category}" configur  ${this.formatDuration(ttl * 1000)}`);
          }
        }
        
        // Sauvegarder la configuration pour rfrence
        localStorage.setItem(this.ttlConfigKey, JSON.stringify({
          defaultTTL: this.defaultTTL,
          categoryTTLs: this.categoryTTLs
        }));
      } else {
        // Si dsactiv, utiliser les valeurs par dfaut mais tenter de charger depuis le localStorage pour la persistance
        const savedConfig = localStorage.getItem(this.ttlConfigKey);
        if (savedConfig) {
          try {
            const { defaultTTL, categoryTTLs } = JSON.parse(savedConfig);
            this.defaultTTL = defaultTTL || this.defaultTTL;
            this.categoryTTLs = categoryTTLs || {};
          } catch (e) {
            console.warn('Impossible de charger la configuration TTL sauvegarde:', e);
          }
        }
      }
    } catch (error) {
      console.error('Erreur lors du chargement des configurations TTL:', error);
    }
  }

  /**
   * Obtient le TTL appropri pour une URL donne en se basant sur les catgories
   * @param {string} url URL de la requte
   * @param {Object} options Options de cache spcifiques  la requte
   * @returns {number} TTL en millisecondes
   */
  getTTLForUrl(url, options = {}) {
    // Si un TTL est explicitement dfini dans les options, l'utiliser
    if (options.ttl && typeof options.ttl === 'number') {
      return options.ttl;
    }
    
    // Vrifier si l'URL correspond  une catgorie configure
    const normalizedUrl = url.toLowerCase();
    
    // Dfinir les catgories d'URL et leurs patterns
    const urlCategories = {
      'cols': /(\/api\/cols|\/api\/passes)/,
      'user': /\/api\/user/,
      'activities': /\/api\/activities/,
      'routes': /\/api\/routes/,
      'weather': /\/api\/weather/,
      'challenges': /\/api\/challenges/,
      'social': /\/api\/social/
    };
    
    // Vrifier si l'URL correspond  une catgorie ayant un TTL spcifique
    for (const [category, pattern] of Object.entries(urlCategories)) {
      if (pattern.test(normalizedUrl) && this.categoryTTLs[category]) {
        return this.categoryTTLs[category];
      }
    }
    
    // Utiliser le TTL par dfaut
    return this.defaultTTL;
  }

  /**
   * Formate une dure en millisecondes en chane de caractres lisible
   * @param {number} ms Dure en millisecondes
   * @returns {string} Dure formate
   */
  formatDuration(ms) {
    if (ms < 1000) return `${ms}ms`;
    if (ms < 60000) return `${Math.round(ms / 1000)}s`;
    if (ms < 3600000) return `${Math.round(ms / 60000)}m`;
    return `${Math.round(ms / 3600000)}h`;
  }
}

// Crer une instance singleton du service
const apiCacheService = new ApiCacheService();

// Exporter les constantes et le service
export { CACHE_STRATEGIES };
export default apiCacheService;
